/* eslint-disable */
import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/productList.vue'
import detail from '../views/productDetail.vue'
import create from '../views/productCreate.vue'
import sales from '../views/SalesList.vue'
import imageInsert from '../views/ProductImageInsert.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/detail',
    name: 'detail',
    component: detail
  },
  {
    path: '/create',
    name: 'create',
    component: create
  },
  {
    path: '/sales',
    name: 'sales',
    component: sales
  },
  {
    path: '/imageInsert',
    name: 'imageInsert',
    component: imageInsert
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
